import React, { memo,  useState } from 'react';
import ReelProducts from './Elements/ReelProducts'
import ReelMedia from './Elements/ReelMedia'
import { Swiper, SwiperSlide } from 'swiper/react'
import ReelBranding from './Elements/ReelBranding';
import { Pagination } from 'swiper/modules';
import 'swiper/css';

const ReelBody = ({ wall, item, onsetProductImg, openAddtocart, setOnProductDetails, isFreePlan, index, currentVideo, muteToggle, muted }) => {
    const autoPlay = ([3,5].includes(item.type)) && currentVideo ? true : false
    const { ThemeInfo, ProductSetting } = wall
    const [activeIndex, setactiveIndex] = useState(0)

    const onUpdateProdImgIdx=(Idx)=>{
        setactiveIndex(Idx)
    }

    const GetProducts=(products)=>{

        return(<Swiper
            autoHeight={true}
            speed={400}
            edgeSwipeDetection={true}
            slidesPerView={products.products && products.products.length > 1 ? 1.3 : 1}
            touchRatio={2}
            spaceBetween={0}
            mousewheel={{ forceToAxis: true, sensitivity: 2 }}
            watchSlidesProgress={true}
            direction='horizontal'
            modules={[Pagination]}
            pagination={{
                clickable: true,
            }}
            className="tb_rpp_wrap">
            {products.products && products.products.map((product, index) => {
                return <SwiperSlide key={index}>
                    <ReelProducts product={product} openAddtocart={openAddtocart} onsetProductImg={onsetProductImg} wall={wall} item={item} setOnProductDetails={setOnProductDetails} />
                </SwiperSlide>
            }) }
        </Swiper>
        )
    }

    return (
        <>
            <ReelMedia  Post={item} wallID={ThemeInfo.wallId} onUpdateProdImgIdx={onUpdateProdImgIdx} ownerId={ThemeInfo.ownerId} ThemeID={ThemeInfo.id} wall={wall} muted={muted} autoPlay={autoPlay} index={index} />
            <div className='tb_rp_content'>  
                { ProductSetting &&  item.isProduct  ? <>
                <GetProducts products={ item.mediaList?.length ? item.mediaList?.[activeIndex]?.product?.length ? item.mediaList[activeIndex].product : [] : item.products} /> </> : null }
                {autoPlay ?
                    <div className="tb_rp_mute_btn" onClick={muteToggle}>
                        <div className={`tb__icon tb-${!muted ? `unmute` : `mute`}`}><div></div></div>
                    </div>
                    : null}

                {(ThemeInfo.branding && ThemeInfo.branding == 1) || isFreePlan ? <ReelBranding wallId={ThemeInfo.wallId} themeID={ThemeInfo.id} /> : null}
            </div>
        </>
    );
}
export default memo(ReelBody);