import React, { PureComponent } from 'react';
import { connect } from 'react-redux'; 
import './scss/ReelPopup.scss'
import PostPopUp from './PostPopUp'
import { closePopUP } from '../../../actions/OtherActions';

class ReelPopUp extends PureComponent {
  state = { isEnable: 0, eventData: {}, shareText: "SHARE", viewOnText: "View on", currentIndex: null, completeData: {}, personalization: {}, url: null, imgData: [] }

  closeCommercePopUP = event => {
    this.setState({ isEnable: 0, currentIndex: null }, () => this.props.closePopUP());
    var taggBoxRoot = document.getElementsByTagName('html');
    if (taggBoxRoot && taggBoxRoot[0]) taggBoxRoot[0].style.overflow = "";
  };

  getCommercePopUPEvent = (data) => {
    if (data?.card) this.setState({ url: "", isEnable: 1, eventData: data, currentIndex: data.index, completeData: data.idArray, viewOnText: data.viewOnText, shareText: data.shareText, windowParent: data, imgData: data.imgData, personalization: data.personalization }, () => this.CommercePopUPSlideWithArrowKey());
  };

  /* POP SLIDER CHANGE INDEX WITH SLIDE */
  featuredPopSliderChangeIndex = (currentIndex) => this.updateCurrentInde(currentIndex);
  SliderWithKey = (currentIndex) => this.updateCurrentInde(currentIndex)

  updateCurrentInde=(currentIndex)=>{
    const { eventData } = this.state;
    if (currentIndex === -1) this.setState({ currentIndex: eventData.idArray.length - 1 });
    else this.setState({ currentIndex: eventData.idArray.length === currentIndex ? 0 : currentIndex });
  }

  componentDidMount() {
    const { data } = this.props;
    this.getCommercePopUPEvent(data);
    var taggBoxRoot = document.getElementsByTagName('html');
    if (taggBoxRoot && taggBoxRoot[0])  taggBoxRoot[0].style.overflow = "hidden";
  }

  componentDidUpdate(prevProps) {
    if (this.props.data !== prevProps.data) this.getCommercePopUPEvent(this.props.data)
  }

  /* POP SLIDER WITH ARROW KEYS EVENTS */
  CommercePopUPSlideWithArrowKey = e => {
    const { personalization } = this.state;

    if (personalization && personalization.popupSlideShow === 1) {
      e = e || window.event;
      let self = this;
      if (e && e.keyCode && e.keyCode !== undefined) {
        if (e.keyCode === 37) self.SliderWithKey(self.state.currentIndex - 1)
        else if (e.keyCode === 39) self.SliderWithKey(self.state.currentIndex + 1)
        else if (e.keyCode === 27) this.closeCommercePopUP();
      }
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    let { completeData, eventData } = this.state
    if (completeData.length != postData.postData.length) {
      let updatePostData = postData.postData.map(pItem => postData.completeDataObject[pItem])
      let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
      eventData.idArray = filteredPostData
      this.setState({ completeData: filteredPostData, eventData }, () => {
        const { currentIndex } = this.state;
        this.setState({ currentIndex: currentIndex + 1 })
      })
    }
  }

  render() {
    const { viewOnText, isEnable, currentIndex, personalization, completeData, eventData, url, imgData } = this.state;
    const { postData, wall, webFilters } = this.props
    let data = null;
    if (currentIndex === -1) data = completeData[eventData.idArray.length - 1];
    else data = completeData[currentIndex];
    return isEnable === 1 && wall &&  Object(completeData || {}).length > 0 ? <PostPopUp webFilters={webFilters} wall={wall} item={data} completeData={completeData} onClosePopUp={this.closeCommercePopUP} currentIndex={currentIndex} personalization={personalization} viewOnText={viewOnText} slideWithArrowKeys={this.CommercePopUPSlideWithArrowKey} featuredPopSliderChangeIndex={this.featuredPopSliderChangeIndex} hostUrl={url} imgData={imgData} isFreePlan={postData && postData.isFreeAdsStatus} />
      : null
  }
}


const mapStateToProps = state => {
  return {
    wall: state.appData.wall, webFilters: state.appData.webFilters,
  }
}
const mapDispatchToProps = dispatch => { return { closePopUP: (data) => dispatch(closePopUP(data)), } }
export default connect(mapStateToProps, mapDispatchToProps)(ReelPopUp);

