import React, { useState, memo } from 'react' 
import { fetchShopifyProduct } from '../../../../actions/cartActions';
import { themePostTracking } from '../../../../actions/moredataAction';

const ReelProducts = ({ product, wall, onsetProductImg, openAddtocart, item, setOnProductDetails }) => {

    const { ThemeInfo, ProductSetting } = wall
    const [noImg, setNoImg] = useState(false)
    const [loading, setLoading] = useState(false)
    const tracking = (product) => themePostTracking({ type: 1, action: 2, wall: ThemeInfo.webId, product_id: product.id, post: item.referenceId, owner: ThemeInfo.ownerId, feed: item.feedId, currency: product.currency, price: product.discount > 0 ? product.discount : product.price })
    const Button = ProductSetting.Button;

    const checkForShopify = async () => {
        setLoading(true)
        const itemDetails = {};
        if (product && product.url) {
            try {
                if (ThemeInfo.ownerId == 140717 || ProductSetting?.Button?.pdp_target == 1) {
                    openUrl(product.url)
                    onsetProductImg(product.image)
                    setLoading(false)
                    tracking(wall, product)
                }
                else { 
                    const isMagento = product.createdFrom == "magento" || false
                    if (product.createdFrom != "") {
                        fetchShopifyProduct(product.url, isMagento, { productId: product.sku, storeId: product.storeId })
                            .then(res => {
                                if (isMagento && res.status == false) {
                                    itemDetails[product.id] = {
                                        data: { available: true },
                                        showCart: ThemeInfo.ownerId !== 140717
                                    };
                                }
                                else {
                                    const productData = isMagento && res?.data?.data || res?.data;
                                    const formatProduct = isMagento ? { available: true } : {}
                                    itemDetails[product.id] = {
                                        data: productData?.id ? { ...productData, ...formatProduct } : null,
                                        showCart: productData?.id && ThemeInfo.ownerId !== 140717
                                    };
                                }

                                onsetProductImg(res?.data?.featured_image ? res.data.featured_image : product.image)
                                setLoading(false)
                                setOnProductDetails(itemDetails)
                                openAddtocart(product.id, product)
                            })
                            .catch(error => {
                                console.log('error', error)
                                openUrl(product.url)
                                onsetProductImg(product.image)
                                setLoading(false)
                                tracking(wall, product)
                            });
                    } else {
                        openUrl(product.url)
                        onsetProductImg(product.image)
                        setLoading(false)
                        tracking(wall, product)
                    }
                }

            } catch (error) {
                console.log('error', error)
                openUrl(product.url)
                setLoading(false)

            }
        }
    }
    const openUrl = (url) => {
        const { Button } = ProductSetting
        var newUrl = url;
        if (parseInt(Button.newTab) == 2) window.parent.location.href = newUrl
        else window.open(newUrl, '_blank');
    }

    return <div className={`tb_rpp_pro ${noImg ? "ts_reelTheme_No_Product" : ""}`}>
        {!noImg ? <div className='tb_rpp_pro_img'>
            <img src={product.image} alt="" onError={() => setNoImg(true)} />
        </div> : null}
        <div className="tb_rpp_pro_cont">
            <div className="tb_rpp_pro_title">
                {product.title}
            </div>
            {product.price > 0 || product.discount > 0 ?
                <div className="tb_rpp_p_wrap">
                    <div className="tb_rpp_p">
                        {product.currency}{product.discount > 0 ? product.discount : product.price}
                    </div>
                    {product.discount == 0 || product.price == product.discount ? <></> :
                        <div className="tb_rpp_p tb_rpp_d_p">
                            {product.currency}{product.price}
                        </div>}
                </div> : null}
            <div className={`tb_rpp_btn${loading ? ' tb_rpp_btn_spinner' : ""}`} disabled={loading} onClick={() => { checkForShopify(); }}>{Button.text} </div>

        </div>
    </div>  
}

export default memo(ReelProducts)