import React, { Suspense, memo } from "react";
import { isTagShop } from "../../../../actions/api";

const Image = React.lazy(() => import("../../../Themes/Elements/Media/Image"));
const ReelVideo = React.lazy(() => import("./ReelVideo"));
const ReelMediaSlider = React.lazy(() => import("./ReelMediaSlider"));
const ReelMedia = (props) => {
  const { Post, muted, autoPlay, index, wallID } = props;
  const size = false;
  
  return (
    <div className="tb_rp_media_wrap"
      role="button"
      tabIndex="0"
      aria-label="Pop Up Button"
      style={{minHeight:window.innerHeight}}
    >
      {Post.mediaList && Post.mediaList.length > 0 ? (
        <Suspense fallback={<></>}>
          <ReelMediaSlider Post={Post} {...props} />
        </Suspense>
      ) : [2,4].includes(Post.type) || !autoPlay ? (
        <Suspense fallback={<></>}>
          <Image
            ImageClass="tb_rp_image"
            Post={Post}
            size={size}
            hotspot={isTagShop}
          />
        </Suspense>
      ) : [3,5].includes(Post.type)   ? (
        <Suspense fallback={<></>}>
          <ReelVideo
            VideoClass="tb_rp_video"
            Post={Post}
            size={size}
            wallID={wallID}
            isCover={true}
            controls={false}
            autoPlay={autoPlay}
            muted={muted}
            handleVideoEnded={() => {}}
            index={index}
          />
        </Suspense>
      ) : null}
    </div>
  );
};
export default memo(ReelMedia);
